@import url("https://fonts.googleapis.com/css?family=Lato:300,300i,400,400i,700,700i");
@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");
body {
  font-family: "Montserrat", sans-serif !important;
}
a {
  transition: all 0.3s ease 0s;
}
a {
  cursor: pointer;
}
a:hover {
  text-decoration: none;
}

a:focus {
  outline: medium none;
  outline-offset: 0;
}
a:focus,
a:hover {
  text-decoration: none !important;
}
button {
  outline: none;
}

a:hover {
  color: #333333;
}
/*header*/
.leftside {
  float: left;
}
.rightside {
  float: right;
}
.leftside-remove-space {
  padding-left: 0;
}
.rightside-remove-space {
  padding-right: 0;
}
.rightside-add-space {
  padding-right: 5px;
}
header .top-line {
  border-bottom: 1px solid #d4d4d4;
  border-top: 1px solid #d4d4d4;
}
.language-block,
.currency-block,
.social-icon-block {
  display: inline-block;
}
.language-block .toggle,
.currency-block .toggle {
  border-left: 1px solid #d4d4d4;
  border-right: 1px solid #d4d4d4;
  cursor: pointer;
  display: block;
  font-size: 14px;
  letter-spacing: 0.5px;
  padding: 18px 32px 14px;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.3s ease 0s;
}
.language-block + .currency-block .toggle,
.currency-block + .language-block .toggle {
  border-left: 1px solid #d4d4d4;
  border-right: 1px solid #d4d4d4;
}
.social-icon-block {
  padding-left: 25px;
}
header ul.social-list {
  list-style: none;
  float: left;
  padding-left: 0;
  margin-bottom: 0;
}
header .dropdown-menu {
  border: none;
  border-radius: 0;
}
header .dropdown-menu > li > a:focus,
header .dropdown-menu > li > a:hover {
  background: #e10302;
  color: #fff;
}
ul.social-list li {
  display: inline-block;
  vertical-align: middle;
}

.social-list li + li {
  margin-left: 7px;
}
ul.social-list li a,
.top-button i {
  color: #333333;
  cursor: pointer;
}
ul.social-list li a:hover,
.top-button i:hover {
  color: #e10302;
}
.top-button {
  display: inline-block;
}
.top-button ul.dropdown-menu {
  left: auto;
  right: 0;
}
.top-button i {
  border-left: 1px solid #d4d4d4;
  border-right: 1px solid #d4d4d4;
  display: inline-block;
  font-size: 20px;
  height: 52px;
  line-height: 52px;
  text-align: center;
  width: 70px;
}
.top-button + .top-button {
  margin-left: -4px;
}
header .top-line + div {
  padding-top: 40px;
  padding-bottom: 40px;
}

/*search box*/
.search-box {
  background: #f0eeee none repeat scroll 0 0;
  border: medium none;
  border-radius: 20px 0 0 20px;
  box-shadow: none;
  height: 44px;
}
.search-button {
  background: #f0eeee none repeat scroll 0 0;
  border: medium none;
  border-radius: 0 20px 20px 0;
  line-height: 0;
}
header .form-control:focus {
  border-color: none;
  box-shadow: none;
  outline: 0 none;
}
/*===============================================================*/
.carousel-inner {
  height: 25vmax;
  margin-bottom: 30px;
}
.sticky {
  position: fixed !important;
  top: 0;
  width: 100%;
  z-index: 2;
  background: #e3f2fd !important;
}
#navbar {
  overflow: hidden;
  background-color: #333;
  padding: 0 6vw;
}

#navbar a {
  float: left;
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

#navbar a:hover {
  background-color: #ddd;
  color: black;
}

#navbar a.active {
  background-color: #4caf50;
  color: white;
}
/*===========================================*/
.home.container-fluid .right {
  float: right;
}
.home.container-fluid {
  margin-top: 30px;
  margin-bottom: 40px;
}
#top-header {
  padding: 0 6vw;
}
.home.container-fluid .right {
  padding-left: 1vw;
  padding-right: 6vw;
}
.home.container-fluid .left {
  padding-right: 1vw;
  padding-left: 6vw;
}
/*=======================================*/
.cart-link:hover {
  border: 1px solid #ff4081;
  color: #1a237e;
}
#featured,
#sellers {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}
.fa.fa-heart-o:hover {
  color: red;
}
.cart-link,
.like,
.price {
  margin-top: 25px !important;
}
.card:hover {
  border: 1px solid rgba(255, 68, 0, 0.4);
}
.category-heading {
  background: #212121;
  padding: 12px;
  color: white;
  margin: 50px 0 30px 0;
}
.card.seller-card {
  border: none;
  transition: transform 1s;
}
.seller-card:hover {
  transform: scale(1.1);
  border: 1px solid rgba(255, 68, 0, 0.2);
}
/*=======================================*/

.social {
  position: fixed;
  z-index: 2;
  left: 0;
  top: 150px;
}
.social div {
  margin: 2px;
  padding: 10px;
  cursor: pointer;
  background: #80d9ff21;
}
.social div box-icon {
  transition: all 0.5s ease-out;
}
.social div box-icon:hover {
  transform: scale(1.2);
}

.footer {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  padding: 3vw 4vw 6vw 8vw;
  justify-content: center;
}
.footer ul {
  padding: 0;
}
.footer ul li {
  color: black;
  letter-spacing: 2px;
  list-style-type: none;
}
.footer ul li:not(:first-child):hover {
  color: orangered;
  cursor: pointer;
}
.filters {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}
