@media (max-width: 767px) {
  header .top-line {
    text-align: center;
  }
  header .top-line .leftside {
    float: none;
  }
  header .top-line .rightside {
    float: none;
    border-top: 1px solid #d4d4d4;
  }
  header .top-line .container {
    padding-left: 0;
    padding-right: 0;
  }
  header .top-line .social-icon-block {
    display: block;
    padding-left: 0;
    border-top: 1px solid #d4d4d4;
  }
  header .top-line .social-icon-block:before,
  header .top-line .social-icon-block:after {
    display: table;
    content: "";
    line-height: 0;
  }
  header .top-line .social-icon-block:after {
    clear: both;
  }
  header .top-line .social-icon-block .social-list {
    float: none;
    margin-bottom: 0;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  header .top-line + div {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  header .top-line + div .rightside {
    text-align: center;
    margin-top: 15px;
  }
  header .top-line + div .leftside {
    text-align: center;
  }
  header .top-line + div .leftside,
  header .top-line + div .rightside {
    float: none;
  }
  #logo img {
    margin: 0 auto;
    margin-bottom: 15px;
  }

  .rightside-add-space {
    padding-right: 15px;
  }
  .leftside-remove-space {
    padding-left: 15px;
  }
  .button-view {
    display: none;
  }
  a.quickview {
    display: none !important;
  }
}
.box h3 {
  font-size: 20px;
  padding: 5px;
}

@media (min-width: 768px) and (max-width: 991px) {
  .button-view {
    margin-top: 10px;
  }
}

